<template>
  <div class="onlineMember">
    <v-container
      fluid
      grid-list-xl>

    <!-- Title -->
      <CommonBtnTitle
        :Title="$ln('線上會員')"
      />
      <!-- <CommonTable1
        :Data="tableData1"
      /> -->
      <div
        v-if="loadingStatus"
        class="text-xs-center">
        <MaterialLoadingPage/>
      </div>
      <div v-else>
        <v-data-table
          :headers="tableData1.headers"
          :items="tableData1.records"
          :no-data-text="tableData1.showNoData"
          hide-actions
          class="elevation-1">
          <template
            slot="headerCell"
            slot-scope="{ header }">
            <span class="column" v-text="$ln(header.text)"/>
          </template>
          <template
            slot="items"
            slot-scope="{ item }">
            <!-- <td class="text-xs-center">{{ item.data1 }}</td> -->
            <td class="text-xs-center">{{ item.account }}</td>
            <td class="text-xs-center">{{ item.nickname }}</td>
            <td class="text-xs-center">{{ item.levelName }}</td>
            <td class="text-xs-center">{{ item.ip }}</td>
            <td class="text-xs-center">{{ item.loginTime }}</td>
            <td class="text-xs-center">{{ item.betTime }}</td>
          </template>
        </v-data-table>
      </div>

    </v-container>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'OnlineMember',
  data () {
    return {
      // 頁面載入狀態
      loadingStatus: false,
      // 載入資料列表
      tableData1: {
        show: false,
        showNoData: '目前無資料',
        headers: [
          // { text: '#', value: 'data1', align: 'center', sortable: true },
          { text: '帳號', value: 'account', align: 'center', sortable: false },
          { text: '暱稱', value: 'nickname', align: 'center', sortable: false },
          { text: '職稱', value: 'levelName', align: 'center', sortable: false },
          { text: '登入IP', value: 'ip', align: 'center', sortable: false },
          { text: '登入時間', value: 'loginTime', align: 'center', sortable: false },
          { text: '投注時間', value: 'betTime', align: 'center', sortable: false }
        ],
        records: []
      }
    }
  },
  computed: {
    ...mapGetters({
      getRecordsState: 'report/getRecordsState'
    }),
    pages () {
      if (this.pagination.rowsPerPage == null || this.pagination.totalItems == null) return 0
      return Math.ceil(this.pagination.totalItems / this.pagination.rowsPerPage)
    }
  },
  watch: {
    '$store.state.lang' (value) {
    this.initLanguage()
    },
    getRecordsState (payload) {
      // console.log(payload)
      this.tableData1.records = payload.result
      // 若 API 有當前頁數回傳資料，可設定如下
      // this.pagination.currentPage = payload.result.current_page
      // this.pagination.totalItems = payload.result.total_count
      // this.headers.contents = payload.result.list
    }
  },
  mounted () {
    this.initLanguage()

    this.loadRecords()
  },
  methods: {
    ...mapActions({
      getRecords: 'api/getRecords'
    }),
    // 載入取得線上會員列表表格資料
    loadRecords (page, rowsPerPage) {
      // 載入狀態(開始)
      this.loadingStatus = true
      let params = {
      }
      this.getRecords(params)
        .then(res => {
          if (res.status === 200) {
            // 載入狀態(結束)
            this.loadingStatus = false
          }
        })
        .catch(err => {
          console.error(err)
        })
    },
    // 初始語系
    initLanguage () {
      this.tableData1.showNoData = this.$ln('目前無資料')
    }
  }
}
</script>

<style lang="scss">
</style>
